export const getImagesFromLibraryQuery = /* GraphQL */ `
  query Query($organizationId: String!, $libraryId: String!, $page: Int!) {
    photos(organizationId: $organizationId, libraryId: $libraryId, page: $page) {
      id
      blurhash
      thumbUrl
      url
      name
      height
      width
      views {
        id
        parentId
        name
        url
        thumbUrl
        height
        width
      }
      description
    }
  }
`
