import clsx from 'clsx'
import { originalLabel } from './helpers/originalLabel'
import Check from '../icons/Check'

type Props = {
  image: Image
  original?: boolean
  selected?: boolean
  displayName: boolean
  chooseImage?: (image: Image) => void
  openVersions?: (image: Image) => void
  thumbnail?: boolean
  version?: boolean
  versionSelected?: boolean
}

export default function RasterImage({
  image,
  original,
  selected,
  displayName,
  chooseImage,
  openVersions,
  thumbnail,
  version,
  versionSelected,
}: Props) {
  return (
    <div className="relative flex flex-col gap-1.5">
      {chooseImage && (
        <button
          type="button"
          onClick={() => {
            if (!thumbnail) {
              chooseImage(image)
            }
          }}
          key={image.id}
          className={clsx('relative', thumbnail ? 'cursor-move' : 'overflow-hidden', {
            'h-full': version,
          })}
        >
          {thumbnail && <div className="remove-overlay" />}

          {/* Image */}
          <img
            src={image.thumbUrl}
            className={clsx(
              'rounded transition-transform duration-300 ease-in-out select-none pointer-events-none bg-gray-100 object-cover',
              thumbnail ? 'h-36 w-36' : 'group-hover:scale-[1.02]',
              selected ? 'border-red-400 border-4' : 'border-white',
              { 'h-full': version },
            )}
          />

          {/* Show count if image has versions */}
          {!thumbnail && openVersions && image?.views && image?.views?.length > 0 && (
            <button
              type="button"
              className="cursor-pointer"
              onClick={(e) => {
                e.preventDefault()
                e.stopPropagation()
                openVersions(image)
              }}
            >
              <div className={clsx('versions', versionSelected ? 'border-2 border-primary' : '')}>
                +{image.views.length}
                {versionSelected && (
                  <span className="h-4 w-4 p-0.5 bg-primary rounded-full flex justify-center items-center">
                    <Check />
                  </span>
                )}
              </div>
            </button>
          )}

          <span className={clsx('checkmark', selected && !thumbnail ? 'scale-100 opacity-100' : 'scale-90 opacity-0')}>
            <Check />
          </span>

          {/* Image dimensions */}
          <span className="absolute bottom-2 left-2 text-xs bg-gray-900/80 px-2.5 py-1 rounded-md font-medium pointer-events-none text-gray-100">
            {image.height}{image.width}
          </span>
        </button>
      )}

      {/* Image name */}
      {displayName && (
        <div>
          {image.name} {originalLabel({ original: original })}
        </div>
      )}
    </div>
  )
}
